"use strict";
/// <reference path="../../typings/index.d.ts" />
const Immutable_1 = require('Immutable');
const immutableState = Immutable_1.Map({
    fetching: false,
    data: Immutable_1.Map({}),
    movies: Immutable_1.Map({})
});
exports.queryReducer = (state = immutableState, action) => {
    switch (action.type) {
        case "START_goldberg":
            return state.set("fetching", true);
        case "DONE_goldberg":
            return state
                .set("fetching", false)
                .set("goldberg", Immutable_1.Map(action.response.data.goldberg));
        case "START_movies":
            return state.set("fetching", true);
        case "DONE_movies":
            return state
                .set("fetching", false)
                .set("movies", Immutable_1.Map(action.response.data.movies));
        default:
            return state;
    }
};



